<template>
  <div class="content">
    <el-form
      :inline="true"
      ref="searchForm"
      :model="searchFormData"
      :rules="searchFormRule"
      status-icon
      hide-required-asterisk
      class="search-form"
    >
      <el-form-item label="标题" prop="title">
        <el-input v-model="searchFormData.title" placeholder="标题"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitSearch" :loading="searching">
          查询
        </el-button>
        <el-button type="primary" @click="loadExams">
          重置
        </el-button>
        <el-button type="primary" @click="handleCreateExam">
          新增
        </el-button>
      </el-form-item>
    </el-form>
    <div class="exams-table">
      <el-table :data="exams" border>
        <el-table-column
          prop="id"
          label="答题ID"
          min-width="100"
        ></el-table-column>
        <el-table-column
          prop="title"
          label="标题"
          min-width="200"
        ></el-table-column>
        <el-table-column
          prop="notes"
          label="备注"
          min-width="200"
        ></el-table-column>
        <el-table-column
          prop="answer_time"
          label="答题时间"
          min-width="100"
        ></el-table-column>
        <el-table-column label="开始时间" min-width="200">
          <template slot-scope="scope">
            {{ new Date(scope.row.start_time * 1000).toLocaleString() }}
          </template>
        </el-table-column>
        <el-table-column prop="end_time" label="结束时间" min-width="200">
          <template slot-scope="scope">
            {{ new Date(scope.row.end_time * 1000).toLocaleString() }}
          </template>
        </el-table-column>
        <el-table-column
          prop="radio_count"
          label="单选个数"
          min-width="100"
        ></el-table-column>
        <el-table-column
          prop="radio_score"
          label="单选分数"
          min-width="100"
        ></el-table-column>
        <el-table-column
          prop="multi_count"
          label="多选个数"
          min-width="100"
        ></el-table-column>
        <el-table-column
          prop="multi_score"
          label="多选分数"
          min-width="100"
        ></el-table-column>
        <el-table-column label="操作" fixed="right" min-width="300">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleRank(scope.$index, scope.row)">
              查看排名
            </el-button>
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">
              编辑
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
            >
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      @size-change="handlePageChange"
      @current-change="handlePageChange"
      :current-page.sync="currentPage"
      :page-sizes="[10, 20, 30, 40]"
      :page-size.sync="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalPage"
      background
      class="exams-pagination"
    >
    </el-pagination>
    <el-dialog title="创建答题" :visible.sync="createDialog">
      <el-form
        ref="createForm"
        :model="createFormData"
        :rules="createFormRule"
        status-icon
        hide-required-asterisk
        label-position="left"
        label-width="20%"
      >
        <el-form-item prop="title" label="标题">
          <el-input
            type="text"
            v-model="createFormData.title"
            placeholder="标题"
          />
        </el-form-item>
        <el-form-item prop="note" label="备注">
          <el-input
            type="textarea"
            v-model="createFormData.note"
            placeholder="备注"
          />
        </el-form-item>
        <el-form-item prop="start_time" label="开始时间">
          <el-col :span="12">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="createFormData.start_time"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-col :span="12">
            <el-time-picker
              placeholder="选择时间"
              v-model="createFormData.start_time"
              style="width: 100%;"
            ></el-time-picker>
          </el-col>
        </el-form-item>
        <el-form-item prop="end_time" label="结束时间">
          <el-col :span="12">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="createFormData.end_time"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-col :span="12">
            <el-time-picker
              placeholder="选择时间"
              v-model="createFormData.end_time"
              style="width: 100%;"
            ></el-time-picker>
          </el-col>
        </el-form-item>
        <el-form-item prop="answer_time" label="答题时间">
          <el-input-number
            controls-position="right"
            v-model="createFormData.answer_time"
          ></el-input-number>
        </el-form-item>
        <el-form-item prop="radio_count" label="单选个数">
          <el-input-number
            controls-position="right"
            v-model="createFormData.radio_count"
          ></el-input-number>
        </el-form-item>
        <el-form-item prop="radio_score" label="单选分数">
          <el-input-number
            controls-position="right"
            v-model="createFormData.radio_score"
          ></el-input-number>
        </el-form-item>
        <el-form-item prop="multi_count" label="多选个数">
          <el-input-number
            controls-position="right"
            v-model="createFormData.multi_count"
          ></el-input-number>
        </el-form-item>
        <el-form-item prop="multi_score" label="多选分数">
          <el-input-number
            controls-position="right"
            v-model="createFormData.multi_score"
          ></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="createDialog = false">
          取 消
        </el-button>
        <el-button type="primary" @click="submitCreate()" :loading="editing">
          确定
        </el-button>
      </div>
    </el-dialog>
    <el-dialog title="修改答题" :visible.sync="editDialog">
      <el-form
        ref="editForm"
        :model="editFormData"
        :rules="editFormRule"
        status-icon
        hide-required-asterisk
        label-position="left"
        label-width="20%"
      >
        <el-input type="hidden" v-model="editFormData.id"></el-input>
        <el-form-item prop="start_time" label="开始时间">
          <el-col :span="12">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="editFormData.start_time"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-col :span="12">
            <el-time-picker
              placeholder="选择时间"
              v-model="editFormData.start_time"
              style="width: 100%;"
            ></el-time-picker>
          </el-col>
        </el-form-item>
        <el-form-item prop="end_time" label="结束时间">
          <el-col :span="12">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="editFormData.end_time"
              style="width: 100%;"
            ></el-date-picker>
          </el-col>
          <el-col :span="12">
            <el-time-picker
              placeholder="选择时间"
              v-model="editFormData.end_time"
              style="width: 100%;"
            ></el-time-picker>
          </el-col>
        </el-form-item>
        <el-form-item prop="answer_time" label="答题时间">
          <el-input-number
            controls-position="right"
            v-model="editFormData.answer_time"
          ></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editDialog = false">
          取 消
        </el-button>
        <el-button type="primary" @click="submitEdit()" :loading="editing">
          确定
        </el-button>
      </div>
    </el-dialog>
    <el-dialog title="排名" :visible.sync="rankDialog">
      <el-table :data="ranks" border>
        <el-table-column prop="uid" label="用户ID"></el-table-column>
        <el-table-column prop="username" label="用户名"></el-table-column>
        <el-table-column prop="rank" label="排名"></el-table-column>
        <el-table-column prop="score" label="分数"></el-table-column>
        <el-table-column prop="use_time" label="用时"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "back-exams",
  data() {
    return {
      ranks: [],
      currentPage: 1,
      totalPage: 1,
      pageSize: 10,
      createDialog: false,
      editDialog: false,
      rankDialog: false,
      editing: false,
      exams: [],
      searching: false,
      searchFormData: {
        title: ""
      },
      searchFormRule: {
        title: [
          {
            required: true,
            message: "请输入标题",
            trigger: "blur"
          }
        ]
      },
      createFormData: {
        title: "",
        notes: "",
        start_time: 0,
        end_time: 0,
        answer_time: 10,
        radio_count: 0,
        multi_count: 0,
        radio_score: 0,
        multi_score: 0
      },
      createFormRule: {},
      editFormData: {
        id: 0,
        start_time: 0,
        end_time: 0,
        answer_time: 0
      },
      editFormRule: {}
    };
  },
  methods: {
    ...mapActions("admin_exams", [
      "getExams",
      "deleteExam",
      "editExam",
      "searchExam",
      "createExam"
    ]),
    ...mapActions("admin_rank", ["getRanks"]),
    handleDelete(index, exam) {
      this.$confirm("确认删除？此操作不可逆！", "删除", {
        confirmButtonText: "删除",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteExam(exam.id)
            .then(res => {
              this.$delete(
                this.exams,
                this.exams.findIndex(item => item.id === exam.id)
              );
              this.$notify.success({
                title: "删除成功",
                message: "删除成功"
              });
            })
            .catch(err => {
              this.$notify.error({
                title: "删除失败",
                message: err.data.error
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    handleEdit(index, exam) {
      this.editDialog = true;
      this.editFormData.id = exam.id;
      this.editFormData.answer_time = exam.answer_time;
      this.editFormData.start_time = new Date(exam.start_time * 1000);
      this.editFormData.end_time = new Date(exam.end_time * 1000);
    },
    submitCreate() {
      this.$refs.createForm.validate(valid => {
        if (valid) {
          this.editing = true;
          let data = { ...this.createFormData };
          data.start_time = Math.round(data.start_time / 1000);
          data.end_time = Math.round(data.end_time / 1000);
          this.createExam(data)
            .then(res => {
              this.editing = false;
              this.createDialog = false;
              // add to view
              this.loadExams();
              this.$notify.success({
                title: "添加成功",
                message: "添加成功"
              });
            })
            .catch(err => {
              this.editing = false;
              this.$notify.error({
                title: "添加失败",
                message: err.data.error
              });
            });
        }
      });
    },
    submitEdit() {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.editing = true;
          let data = { ...this.editFormData };
          data.start_time = Math.round(data.start_time / 1000);
          data.end_time = Math.round(data.end_time / 1000);
          this.editExam(data)
            .then(res => {
              this.editing = false;
              this.editDialog = false;
              let index = this.exams.findIndex(
                item => item.id === this.editFormData.id
              );
              this.exams[index].start_time = this.editFormData.start_time;
              this.exams[index].end_time = this.editFormData.end_time;
              this.exams[index].answer_time = this.editFormData.answer_time;
              this.$notify.success({
                title: "修改成功",
                message: "修改成功"
              });
            })
            .catch(err => {
              this.editing = false;
              this.$notify.error({
                title: "修改失败",
                message: err.data.error
              });
            });
        }
      });
    },
    handlePageChange(size) {
      this.getExams({
        page: this.currentPage,
        pre_page: this.pageSize
      })
        .then(res => {
          this.exams = res.data.data.exams;
          this.totalPage = res.data.data.count;
        })
        .catch(err => {
          this.$notify.error({
            title: "获取失败",
            message: err.data.error
          });
        });
    },
    submitSearch() {
      this.$refs.searchForm.validate(valid => {
        if (valid) {
          this.searchExam(this.searchFormData)
            .then(res => {
              this.exams = res.data.data;
              this.totalPage = this.exams.length;
              this.pageSize = this.exams.length;
            })
            .catch(err => {
              this.$notify.error({
                title: "获取失败",
                message: err.data.error
              });
            });
        }
      });
    },
    loadExams() {
      this.searchFormData.title = "";
      this.pageSize = 10;
      this.currentPage = 1;
      this.getExams({
        page: 1,
        pre_page: 10
      })
        .then(res => {
          this.exams = res.data.data.exams;
          this.totalPage = res.data.data.count;
        })
        .catch(err => {
          this.$notify.error({
            title: "获取失败",
            message: err.data.error
          });
        });
    },
    handleCreateExam() {
      this.createDialog = true;
      this.$set(this, "createFormData", {
        id: -1,
        title: "",
        notes: "",
        start_time: new Date(),
        end_time: new Date(),
        answer_time: 10,
        radio_count: 0,
        multi_count: 0,
        radio_score: 0,
        multi_score: 0
      });
    },
    handleRank(index, exam) {
      this.rankDialog = true;
      this.getRanks(exam.id)
        .then(res => {
          this.ranks = res.data.data;
        })
        .catch(err => {
          this.$notify.error({
            title: "获取失败",
            message: err.data.error
          });
        });
    }
  },
  mounted() {
    this.loadExams();
  }
};
</script>

<style lang="scss" scoped>
.search-form {
  margin-top: 22px;
  margin-left: 1rem;
}

.exams-table {
  margin-left: 1rem;
  margin-right: 1rem;
}

.exams-pagination {
  margin: 22px 1rem;
}
</style>
